<template>
  <div class="main">
    <div class="page-main bl bt br hi ml20 mt20 mr20 p0  df">
      <div class="br stsyem-post" style="width:218px;min-width:218px">
        <div class="tit">
          系统默认
        </div>
        <div class="item">
          <p v-for="item in defaultTab" @click="changeTab(item)">{{item}}</p>
        </div>
        <div class="tit clearfix pr20 bt">
          <span class="fl">自定义职务</span> <a @click="addMemberShow" style='cursor: pointer' class="ml30 fr no-decoration">+新增职务</a>
        </div>
        <div class="item">
          <p v-for="item in personalTab" @click="changeTab(item)">
            <span class="fl">{{item}}</span>
            <i class="el-icon-delete fr ml10" @click.stop="deleteTab(item)"></i>
            <i class="el-icon-edit fr  ml10" @click.stop="editTab(item)"></i>
          </p>
        </div>
      </div>
      <div class="flex1 pl30 pr30">
        <el-tabs v-model="activeName" class="authority-tab">
          <el-tab-pane label="职务成员" name="first">
            <div class='action-bar'>
              <el-button type='primary' size='small' @click="addMemberShow">添加人员</el-button>
              <el-button type='default' size='small' @click="deleteSelectMember">批量删除</el-button>
            </div>
            <el-table
              :data="tableData4"
              stripe
              border
              type='selection'
              @select="select"
              @select-all="select">
              <el-table-column
                type="selection"
                fixed="left"
                width="40">
              </el-table-column>
              <el-table-column
                v-for="item in columns"
                :prop="item.key"
                :label="item.title"
                :formatter="item.formate"
                :key="item.key"
                align="center"
                :width="item.width">
              </el-table-column>
              <el-table-column
                fixed="right"
                prop="zip"
                label="操作"
                align="center"
                width="220">
                <template slot-scope="scope">
                  <el-button type="text" @click='handleRemoveClick(scope.row)'>移除</el-button>
                </template>
              </el-table-column>
            </el-table>
          </el-tab-pane>
          <el-tab-pane label="功能权限" name="second">
            <el-form ref="form" :model="permissions" class="form-text f13 " label-width="108px" size="mini">
            <el-col :span="24">
              <el-form-item label="管理数据范围">
                <el-radio-group v-model="permissions.dataManage">
                  <el-col :span="24" class="mt5 ml20">
                    <el-radio :label="1">全部</el-radio>
                  </el-col>
                  <el-col :span="24" class="mt10 ml20">
                    <el-radio :label="2">所在部门及下级部门</el-radio>
                  </el-col>
                  <el-col :span="24" class="mt10 ml20">
                    <el-radio :label="3">所在部门</el-radio>
                  </el-col>
                  <el-col :span="24" class="mt10 ml20">
                    <el-radio :label="4">仅本人相关</el-radio>
                  </el-col>
                </el-radio-group>
              </el-form-item>
            </el-col>
            </el-form>
          </el-tab-pane>
        </el-tabs>
      </div>
    </div>
    <AddDialog @closeDialog="closeAddDialog" :dialogFormVisible="addDialogShow"></AddDialog>
  </div>
</template>

<script>
  import AddDialog from './AddDialog.vue'
  export default {
    components: {
      AddDialog
    },
    data() {
      return {
        addDialogShow: false,
        selectArr: [],
        selectTab: '',
        activeName: 'first',
        defaultTab: ['系统管理员', '部门主管', '资产管理人', '运维管理员', '成员'],
        personalTab: ['系统管理员'],
        columns: [
          {title: '姓名', key: 'name',},
          {title: '工号/帐号', key: 'id'},
          {title: '部门', key: 'department'},
          {title: '手机号', key: 'phone'},
          {title: '微信', key: 'weChat'},
        ],
        permissions: {
          dataManage: '',
          funcManage: {

          }
        },
        tableData4: [{
          date: '2016-05-03',
          name: '王小虎',
          province: '上海',
          city: '普陀区',
          address: '上海市普陀区金沙江路 1518 弄',
          zip: 200333
        }, {
          date: '2016-05-02',
          name: '王小虎',
          province: '上海',
          city: '普陀区',
          address: '上海市普陀区金沙江路 1518 弄',
          zip: 200333
        }, {
          date: '2016-05-04',
          name: '王小虎',
          province: '上海',
          city: '普陀区',
          address: '上海市普陀区金沙江路 1518 弄',
          zip: 200333
        }, {
          date: '2016-05-01',
          name: '王小虎',
          province: '上海',
          city: '普陀区',
          address: '上海市普陀区金沙江路 1518 弄',
          zip: 200333
        }, {
          date: '2016-05-08',
          name: '王小虎',
          province: '上海',
          city: '普陀区',
          address: '上海市普陀区金沙江路 1518 弄',
          zip: 200333
        }, {
          date: '2016-05-06',
          name: '王小虎',
          province: '上海',
          city: '普陀区',
          address: '上海市普陀区金沙江路 1518 弄',
          zip: 200333
        }, {
          date: '2016-05-07',
          name: '王小虎',
          province: '上海',
          city: '普陀区',
          address: '上海市普陀区金沙江路 1518 弄',
          zip: 200333
        }]
      }
    },
    methods: {
      closeAddDialog() {
        this.addDialogShow = false
      },
      addMemberShow() {
        this.addDialogShow = true
      },
      deleteTab(item) {
        let msg = `确定要删除${item || '该'}职务如果删除该职务，原有职务下的员工，对应权限将删除。`
        console.log('删除' + item)
        this.$confirm(msg, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      editTab(item) {
        console.log('editTab' + item)
      },
      changeTab(item) {
        console.log('changeTab' + item)
      },
      select(array) {
        this.selectArr = array
        console.log(this.selectArr)
      },
      deleteSelectMember() {
        let msg = `确定要删除选中的人员的${this.selectTab || '该项'}职务？删除后，用户将看不到对应的功能权限入口，原有数据保留。`
        console.log('删除' + this.selectArr)
        this.$confirm(msg, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      handleRemoveClick(row) {
        console.log('删除' + row.id)
        let msg = `确定要删除该人员的${this.selectTab || '该项'}职务？删除后，用户将看不到对应的功能权限入口，原有数据保留。`
        this.$confirm(msg, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      }
    }
  };
</script>
